.item {
    background-image: url(./ui_crafting.png);
    width: 32px;
    height: 32px;
    display: inline-block;
    position: relative;
    cursor: pointer
}

.item:before {
    display: none;
    position: absolute;
    top: -44px;
    left: -71.5px;
    right: 0;
    width: 175px;
    background: #000;
    padding: 8px 4px;
    font-size: 13px;
    margin: auto;
    z-index: 111111111;
    box-sizing: border-box;
    text-align: center;
    color: red;
    box-shadow: 0 8px 17px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19)
}

.item:hover:before {
    display: block
}

